import { Component, OnInit } from '@angular/core';
import { TenantService } from '../../services/tenant.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router, ActivatedRoute } from '@angular/router';
import { Tenant } from '../../models/Tenant';
import { NgForm } from '@angular/forms';
import { SettingsService } from '../../services/settings.service';

@Component({
  selector: 'app-edit-tenant',
  templateUrl: './edit-tenant.component.html',
  styleUrls: ['./edit-tenant.component.css']
})
export class EditTenantComponent implements OnInit {

  tenantId: string;
  tenant: Tenant = {
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    balance: 0
  };
  disableBalanceOnEdit: boolean = true;

  constructor(private _tenantService: TenantService,
              private _flashMessagesService: FlashMessagesService,
              private _routerService: Router,
              private _activatedRoute: ActivatedRoute,            
              private settingsService:SettingsService) { }

  ngOnInit() {
    this.tenantId = this._activatedRoute.snapshot.paramMap.get('id');
    console.log(this.tenantId);
    this._tenantService.getTenantById(this.tenantId).valueChanges().subscribe((tenant)=>{
      this.tenant = tenant;
    }, (error) => {
      console.log("Error in Edit tenant details");
    });
    this.disableBalanceOnEdit = this.settingsService.getSettings().disableBalanceOnEdit;
  }

  submitForm(editTenantFormData: NgForm){
    console.log("*****In submit Handler***** "+ Object.keys(editTenantFormData.form));
    let flashMessagesService = this._flashMessagesService;
    if(editTenantFormData.form.valid){
        let tenantData = Object.assign({}, editTenantFormData.form.value);
        tenantData.balance = tenantData.balance? tenantData.balance: this.tenant.balance;
        this._tenantService.updateTenant(this.tenantId,tenantData).then((reponse) => {
          flashMessagesService.show('Tenant Details updated successfully', { cssClass: 'alert-success', timeout: 4000 });
          this._routerService.navigate(['/tenant', this.tenantId]);
        },(error)=>{        
          flashMessagesService.show(error.message, { cssClass: 'alert-danger', timeout: 4000 });  
          this._routerService.navigate(['/edittenant', this.tenantId]);
        });
    }else{
      console.log("****In submit Handler****** "+ editTenantFormData.form.valid );
    }    
  }
}
