import { Component, OnInit } from '@angular/core';
import { TenantService } from '../../services/tenant.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router, ActivatedRoute } from '@angular/router';
import { Tenant } from '../../models/Tenant';

@Component({
  selector: 'app-tenant-details',
  templateUrl: './tenant-details.component.html',
  styleUrls: ['./tenant-details.component.css']
})
export class TenantDetailsComponent implements OnInit {

  tenantId: string;
  tenant: Tenant;
  hasBalance: boolean = false;
  showBalanceUpdateInput: boolean = false;

  constructor(private _tenantService: TenantService,
              private _flashMessagesService: FlashMessagesService,
              private _routerService: Router,
              private _activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.tenantId = this._activatedRoute.snapshot.paramMap.get('id');
    console.log(this.tenantId);
    this._tenantService.getTenantById(this.tenantId).valueChanges().subscribe((tenant)=>{
      this.tenant = tenant;
      this.updateCurrencyColour();
    }, (error) => {
      console.log("Error in Edit tenant details"+error);
    });
  }

  updateTenantBalance(tenantId: string){
    this._tenantService.updateTenantBalance(tenantId, this.tenant).then((response)=>{
      this.showBalanceUpdateInput = false;
      this.updateCurrencyColour();
      this._flashMessagesService.show('Tenant Balance updated successfully', { cssClass:'alert-success', timeout: 3000});
    });    
    this._routerService.navigate(['/tenant/', tenantId]);
  }

  updateCurrencyColour(){
    if(this.tenant && this.tenant.balance > 0){
      this.hasBalance = true;
    }else{
      this.hasBalance = false;
    }
  }

  onDeleteClick(){
    if(confirm('Are you sure you want to delete the tenant?')){
      this._tenantService.deleteTenant(this.tenantId).then((response)=>{
        console.log(response);
        this._routerService.navigate(['/']);
      });
    }
  }
}
