import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList , AngularFireObject  } from '@angular/fire/database';
import { Observable } from 'rxjs';
import { Tenant } from '../models/Tenant';

@Injectable()
export class TenantService {
  tenants: AngularFireList<any>;
  tenant: AngularFireObject<any>;
  
  constructor(private af: AngularFireDatabase) {
    this.tenants = this.af.list('/tenants') as AngularFireList<Tenant[]>;
   }

   getTenants(){
     return this.tenants;
   }

   addTenant(tenant: Tenant): any{
     return this.tenants.push(tenant);
   }

   getTenantById(tenantId: string){      
      this.tenant =  this.af.object('/tenants/'+tenantId) as AngularFireObject<Tenant>;
      return this.tenant;
   }

   updateTenantBalance(tenantId: string, tenant: Tenant){
     let updatedBalance = tenant.balance;
     return this.tenants.update(tenantId, { balance: updatedBalance});
   }

   deleteTenant(tenantId: string){
      return this.tenants.remove(tenantId);
   }

   updateTenant(tenantId: string, tenant: Tenant){
    return this.tenants.set(tenantId, tenant);
   }
}
