import { Component, OnInit,  AfterContentChecked } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Tenant } from '../../models/Tenant';
import { TenantService } from '../../services/tenant.service';
import { Observable } from 'rxjs';
import * as _ from 'lodash';

@Component({
  selector: 'app-tenants',
  templateUrl: './tenants.component.html',
  styleUrls: ['./tenants.component.css']
})
export class TenantsComponent implements OnInit,  AfterContentChecked {

  tenants: Tenant[];
  totalBalance: number;
  addStatus: string;

  constructor(private tenantService: TenantService,
              private _route: ActivatedRoute,
              private _flashMessagesService: FlashMessagesService ) {    
    this.totalBalance = 0;
  }

  ngOnInit() { 
    this.tenantService.getTenants().snapshotChanges().subscribe(
      (angularFirelist) => {
        this.tenants = [];        
        this.handleSuccess(angularFirelist);}, 
      (error) => this.handleError(error)
    ); 
    //TODO: this.addStatus = this._route.snapshot.queryParams['add'];   
  }

  ngAfterContentChecked() {    
    //TODO: console.log("Tenant add status :"+this.addStatus);
  }
  handleSuccess(angularFirelist){
    console.log("this._flashMessagesService"+ this._flashMessagesService); 
    console.log("****Tenants**** "+ JSON.stringify(angularFirelist));
    _.forEach(angularFirelist, (ngFirelistObject)=>{
        let payload = JSON.stringify(ngFirelistObject.payload);
        console.log(payload);
        this.tenants.push({
          '$key': ngFirelistObject['key'],
          ...JSON.parse(payload)
        } as Tenant);
    });
    this.calculateTotalBalance();
  }
  
  handleError(error){
    console.log(error);
  }

  calculateTotalBalance(){
    this.totalBalance = 0;
    console.log("******Calculate balance****"+ this.totalBalance);
    _.forEach(this.tenants, (tenant) =>{
      this.totalBalance += parseFloat(tenant.balance? tenant.balance : 0);
    });
    console.log(this.totalBalance);
  }

}
